import React from "react";

import arrow from "assets/img/link-arrow-gray.svg";
import {
  StyledLink,
  Container,
  Content,
  IconContainer,
  Title,
} from "./elements";

const ImageLink = ({ link }) => (
  <StyledLink to={link.path}>
    <Content image={link.image}>
      <IconContainer>
        <img alt="arrow" src={arrow} />
      </IconContainer>
      <Title>{link.title}</Title>
    </Content>
  </StyledLink>
);

const ImageLinks = ({ leftLink, rightLink }) => (
  <Container>
    <ImageLink link={leftLink} />
    <ImageLink link={rightLink} />
  </Container>
);

export default ImageLinks;
