import React from "react";

import { compose, withStateHandlers } from "recompose";

import { MarketoForm, Container } from "./elements";

const _MarketoLeadForm = ({
  success,
  setSuccess,
  formId,
  successMessage,
  successCallback = () => {},
}) => (
  <Container success={success}>
    <MarketoForm
      munchkinId="166-VEV-188"
      formId={formId}
      successCallback={() => {
        setSuccess();
        successCallback();
      }}
      successMessage={successMessage}
    />
  </Container>
);

const MarketoLeadForm = compose(
  withStateHandlers(
    {
      success: false,
    },
    {
      setSuccess: () => () => ({ success: true }),
    },
  ),
)(_MarketoLeadForm);

export default MarketoLeadForm;
